.header{
    height: auto;
    background: #79a3f7;
    padding-bottom: 30px;
    
    
}

.catalog{
    width: 50px;
    height: 50px;
    
    margin-top: 10px;
    float: left;
    
}

.catalog img:hover{
    border: 1px solid blue;
    border-radius: 50%;
}

.catalogBox{
    position: absolute;
    z-index: 9999;
    width: 350px;
    height: auto;
    border: 2px solid silver;
    margin-left: 50px;
    padding-left: 10px;
    background: white;
    border-radius: 10px;
    padding-bottom: 10px;
    
    
    
}

.catalogShow{
    display: none;
}

.search{
    float: left;
}

.loop{
    height: 38px;
    
    
}

.basket{
    float: right;
    margin-right: 50px;
    border-radius: 30px;
    background-image: url('../../img/basket.jpg') ;
    width: 50px;
    height: 50px;
    background-size: 50px;
}

